<style lang="less" scoped>
.students-list {
  width: 100%;
  user-select: none;
  .box {
    width: 100%;
    height: 815px;
    margin-top: 22px;
    .no-mg-r {
      margin-right: 0 !important;
    }
    .student-item {
      width: 214.7px;
      height: 82px;
      float: left;
      background: #fff;
      margin-right: 22px;
      margin-bottom: 22px;
      box-shadow: 0 2px 8px #ddd;
      padding: 14px 12px;
      .img-box {
        width: 52px;
        height: 52px;
        float: left;
        border-radius: 50%;
        overflow: hidden;
        img {
          width: 100%;
          height: 100%;
        }
        &:hover {
          cursor: pointer;
        }
      }
      .top {
        width: 72%;
        padding-left: 12px;
        float: left;
        height: 22px;
        line-height: 22px;
        .name {
          width: 60%;
          text-align: left;
          float: left;
          &:hover {
            cursor: pointer;
          }
        }
        .iconfont {
          float: right;
          color: #aaa;
          margin-left: 8px;
          &:hover {
            cursor: pointer;
          }
        }
      }
      .bottom {
        width: 72%;
        padding-left: 12px;
        float: left;
        padding-top: 12px;
        color: #aaa;
        display: flex;
        justify-content: space-between;
        user-select: none;
        .status-badge {
          font-size: 0.6rem;
          .icon {
            border-radius: 3px;
            padding: 2px 2px 1px;
            color: #fff;
          }
          .text {
            font-size: 0.7rem;
          }
        }
        &:hover {
          cursor: pointer;
        }
      }
    }
    .choosed {
      background: #63a9ff;
      color: #fff;
      .iconfont,
      .bottom {
        color: #fff !important;
      }
    }
  }
}
</style>



